export const selectSystemAdminRoleLevel = constSelector<number>(0)

export const selectUsCountryId = selector<number>({
  key: 'selectors/selectUsCountryId',
  get: async ({ get }) => {
    const countries = get(selectLookups).countries
    const usCountryId = countries.find((country) => country.name === 'United States')?.id
    if (!usCountryId) throw new Error('failed to determine United States country ID')
    return usCountryId
  },
})

export const selectDefaultOrganizationTypeId = selector<number>({
  key: 'selectors/selectDefaultOrganizationTypeId',
  get: async ({ get }) => {
    const organizationTypes = get(selectLookups).organizationTypes
    const defaultOrganizationTypeId = organizationTypes.find(
      (organizationType) => organizationType.name === 'Other' // TODO: should this be "unknown"?
    )?.id
    if (!defaultOrganizationTypeId) throw new Error('failed to determine default organization type')
    return defaultOrganizationTypeId
  },
})

