import gql from 'graphql-tag'
import { TaxaLookups, SampleLookups, GeoLookups, EntityLookups } from '@namcbugdb/common'

import { Api } from '../api'
import { LifeStage, NoteType, TaxaLevel, BoxState, UsState, Role } from '../../types'

export const fetchLookupsQuery = gql`
  query {
    lookups {
      taxa {
        taxaLevels
        lifeStages
      }
      sample {
        noteTypes
        boxStates
        sampleMethods
        sampleTypes
        towTypes
        effortTypes
        designTypes
      }
      geo {
        habitats
        ecosystems
        waterbodyTypes
        states
        countries
      }
      entity {
        organizationTypes
        roles
      }
    }
  }
`

export const fetchLookups =
  (api: Api) =>
  async (): Promise<{
    taxaLevels: TaxaLevel[]
    lifeStages: LifeStage[]
    noteTypes: NoteType[]
    boxStates: BoxState[]
    sampleMethodNames: string[]
    sampleTypeNames: string[]
    towTypeNames: string[]
    effortTypeNames: string[]
    designTypeNames: string[]
    habitatNames: string[]
    ecosystemNames: string[]
    waterBodyTypeNames: string[]
    usStates: UsState[]
    organizationTypes: EntityLookups['organizationTypes']
    countries: GeoLookups['countries']
    roles: Role[]
  }> => {
    const data = await api.query<{
      lookups: {
        taxa: {
          taxaLevels: TaxaLookups['taxaLevels']
          lifeStages: TaxaLookups['lifeStages']
        }
        sample: {
          noteTypes: SampleLookups['noteTypes']
          boxStates: SampleLookups['boxStates']
          sampleMethods: SampleLookups['sampleMethods']
          sampleTypes: SampleLookups['sampleTypes']
          towTypes: SampleLookups['towTypes']
          effortTypes: SampleLookups['effortTypes']
          designTypes: SampleLookups['designTypes']
        }
        geo: {
          habitats: GeoLookups['habitats']
          ecosystems: GeoLookups['ecosystems']
          waterbodyTypes: GeoLookups['waterbodyTypes']
          states: GeoLookups['states']
          countries: GeoLookups['countries']
        }
        entity: {
          organizationTypes: EntityLookups['organizationTypes']
          roles: EntityLookups['roles']
        }
      }
    }>(fetchLookupsQuery)
    const {
      lookups: {
        taxa: { taxaLevels, lifeStages },
        sample: { noteTypes, boxStates, sampleMethods, sampleTypes, towTypes: towTypeNames, effortTypes, designTypes },
        geo: { habitats, ecosystems, waterbodyTypes, states: usStates, countries },
        entity: { organizationTypes, roles },
      },
    } = data

    const nameExtractor = ({ name }) => name

    return {
      taxaLevels,
      lifeStages,
      noteTypes,
      boxStates,
      sampleMethodNames: sampleMethods.map(nameExtractor),
      sampleTypeNames: sampleTypes.map(nameExtractor),
      towTypeNames,
      effortTypeNames: effortTypes.map(nameExtractor),
      designTypeNames: designTypes.map(nameExtractor),
      habitatNames: habitats.map(nameExtractor),
      ecosystemNames: ecosystems.map(nameExtractor),
      waterBodyTypeNames: waterbodyTypes.map(nameExtractor),
      usStates,
      countries,
      organizationTypes,
      roles,
    }
  }
