import gql from 'graphql-tag'
import { Project as GraphProject } from '@namcbugdb/common'

import { Api } from '../api'
import { Project } from '../../types'

export const fetchProjectsQuery = gql`
  query ($offset: Int!) {
    projects(limit: 500, offset: $offset) {
      records {
        projectId
        projectName
        contactId
        contactName
        description
        sampleCount
        modelIds
        createdDate
        updatedDate
        isPrivate
      }
      nextOffset
    }
  }
`

export const fetchProjects = (api: Api) => async (): Promise<Project[]> => {
  const data = await api.queryAllPages<{ projects: { records: GraphProject[] } }>(
    fetchProjectsQuery,
    {},
    { dataProperty: 'projects' }
  )

  return data.projects.records.map(({ projectId: id, projectName: name, contactName, ...rest }) => ({
    id,
    name,
    contactName: contactName ?? '',
    ...rest,
  }))
}
