import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample, SampleTaxaInfo as GraphSampleTaxaInfo } from '@namcbugdb/common'

import { Api } from '../api'
import { Sample } from '../../types'

export const fetchSampleByIdQuery = gql`
  query ($sampleId: Int!) {
    samples(sampleIds: [$sampleId]) {
      records {
        sampleId
        boxId
        boxState
        measurements
        customerName
        sampleDate
        siteName
        usState
        idErId
        idStartDate
        idEndDate
        sorterId
        sorterName
        sortStartDate
        sortEndDate
        labSplit
        sorterCount
        elutriation
        sortTime
        measurements
      }
    }
    sampleTaxa(sampleIds: [$sampleId]) {
      records {
        sampleId
        organismId
        lifeStageId
        taxonomyId
        levelId
        levelName
        splitCount
        bigRareCount
        noteTypeIds
        comments
      }
    }
  }
`

export const fetchSampleById =
  (api: Api) =>
  async (sampleId: number): Promise<Sample | null> => {
    const data = await api.query<{
      samples: PaginatedRecords<GraphSample>
      sampleTaxa: PaginatedRecords<GraphSampleTaxaInfo>
    }>(fetchSampleByIdQuery, { sampleId })

    const sample = data.samples.records[0]
    if (!sample) return null

    const organisms = data.sampleTaxa.records
    const {
      sampleId: id,
      idErId: identificationUserId,
      idStartDate: identificationStartDate,
      idEndDate: identificationEndDate,
      boxState,
      ...restSample
    } = sample

    return {
      ...restSample,
      id,
      identificationUserId,
      identificationStartDate,
      identificationEndDate,
      boxState,
      organisms: organisms.map((organism) => ({
        ...organism,
        id: organism.organismId,
        taxonId: organism.taxonomyId,
        noteTypeIds: organism.noteTypeIds.filter((noteTypeId) => noteTypeId !== null),
      })),
    }
  }
