/** @jsxImportSource @emotion/react */
import React, { useEffect, useState } from 'react'
import { atom, useRecoilCallback, useRecoilState } from 'recoil'
import { identity, intersectionBy } from 'lodash'
import produce from 'immer'
import MuiButton from '@mui/material/Button'
import AddCircleIcon from '@mui/icons-material/AddCircle'
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle'
import ToggleButton from '@mui/material/ToggleButton'
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup'
// import log from 'loglevel'

import {
  selectBoxSamplesByBoxId,
  selectMinimalSamplesByOrganizationId,
  selectMinimalSamplesByTaxonId,
  selectMinimalSamplesByUsStateId,
  selectProjectSamplesByProjectId,
} from '../../data/selectors'
import { MinimalSample } from '../../types'
import { Filter, FilterChips } from '../Filters'
import { Title } from '../Title'
import { Chip } from '../Chip'
import { LinearProgress } from '../LinearProgress'
import { pluralize } from '../../lib/pluralize'
import { addProjectSamples, fetchMinimalSamplesByIds, removeProjectSamples } from '../../data/calls'
import { useApi } from '../../data/api'
import { useNotifications } from '../AppNotifications'
import { useDeferredInvalidate } from '../../data/selectors/invalidate'
import { DateField } from '../DateField'
import {
  BoxCriterionField,
  CriterionOption,
  MethodCriterionField,
  OrganizationCriterionField,
  ProjectCriterionField,
  SampleIdsCriterionField,
  TaxonomyCriterionField,
  TypeCriterionField,
  UsStateCriterionField,
} from './CriterionFields'

export interface ModifyProjectSamplesProps {
  initialMode: null | 'add' | 'remove'
  onOperatingChange?: (operating: boolean) => void
  onSamplesAdded?: (samples: MinimalSample[]) => void
  onSamplesRemoved?: (samples: MinimalSample[]) => void
  projectId: number
  projectName: string
  samples: MinimalSample[]
}

const minimalSamplesByIdAtom = atom<Record<number, MinimalSample | Promise<MinimalSample | undefined>>>({
  key: '',
  default: {},
})

export const ModifyProjectSamples = ({
  initialMode,
  onOperatingChange,
  onSamplesAdded,
  onSamplesRemoved,
  projectId: thisProjectId,
  projectName: thisProjectName,
  samples: initialSamples,
}: ModifyProjectSamplesProps): JSX.Element => {
  const api = useApi()
  const deferredInvalidate = useDeferredInvalidate()
  const notifications = useNotifications()

  // CRITERIA STATE

  const [boxCriterion, setBoxCriteron] = useState<CriterionOption<number> | null>(null)
  const [usStateCriterion, setUsStateCriterion] = useState<CriterionOption<number> | null>(null)
  const [taxonCriteria, setTaxonCriteria] = useState<CriterionOption<number> | null>(null)
  const [organizationCriterion, setOrganizationCriterion] = useState<CriterionOption<number> | null>(null)
  const [projectCriterion, setProjectCriterion] = useState<CriterionOption<number> | null>(null)
  const [sampleIdsCriterion, setSampleIdsCriterion] = useState<number[]>([])
  const [fromDate, setFromDate] = useState<string | null>(null)
  const [toDate, setToDate] = useState<string | null>(null)
  const [typeCriterion, setTypeCriterion] = useState<CriterionOption<string> | null>(null)
  const [methodCriterion, setMethodCriterion] = useState<CriterionOption<string> | null>(null)

  // OTHER STATE

  const [thisProjectSamples, setThisProjectSamples] = useState(initialSamples)
  const [candidateSamples, setCandidateSamples] = useState<MinimalSample[]>([])
  const [resultsMessage, setResultsMessage] = useState<React.ReactNode>(null)
  const [displayFilters, setDisplayFilters] = useState<Filter<MinimalSample>[]>([])
  const [isOperating, setIsOperating] = useState(false)
  const [mode, setMode] = useState(initialMode)

  const isAddMode = mode === 'add'

  useEffect(() => {
    if (onOperatingChange) onOperatingChange(isOperating)
  }, [isOperating])

  // RECOIL

  const [minimalSamplesById, setMinimalSamplesById] = useRecoilState(minimalSamplesByIdAtom)

  const getSamplesByBoxId = useRecoilCallback<[number], Promise<MinimalSample[]>>(
    ({ snapshot }) =>
      async (boxId: number) =>
        snapshot.getPromise(selectBoxSamplesByBoxId(boxId)).catch((err) => {
          notifications.error(`Failed to retrieve box samples: ${err}`)
          return []
        })
  )

  const getSamplesByProjectId = useRecoilCallback<[number], Promise<MinimalSample[]>>(
    ({ snapshot }) =>
      async (projectId: number) =>
        snapshot.getPromise(selectProjectSamplesByProjectId(projectId)).catch((err) => {
          notifications.error(`Failed to retrieve project samples: ${err}`)
          return []
        })
  )

  const getSamplesByUsStateId = useRecoilCallback<[number], Promise<MinimalSample[]>>(
    ({ snapshot }) =>
      async (usStateId: number) =>
        snapshot.getPromise(selectMinimalSamplesByUsStateId(usStateId)).catch((err) => {
          notifications.error(`Failed to retrieve US State samples: ${err}`)
          return []
        })
  )

  const getSamplesByTaxonId = useRecoilCallback<[number], Promise<MinimalSample[]>>(
    ({ snapshot }) =>
      async (taxonId: number) =>
        snapshot.getPromise(selectMinimalSamplesByTaxonId(taxonId)).catch((err) => {
          notifications.error(`Failed to retrieve taxon samples: ${err}`)
          return []
        })
  )

  const getSamplesByOrganizationId = useRecoilCallback<[number], Promise<MinimalSample[]>>(
    ({ snapshot }) =>
      async (organizationId: number) =>
        snapshot.getPromise(selectMinimalSamplesByOrganizationId(organizationId)).catch((err) => {
          notifications.error(`Failed to retrieve organization samples: ${err}`)
          return []
        })
  )

  useEffect(() => {
    setMinimalSamplesById(
      produce((draft) => {
        initialSamples.forEach((sample) => {
          draft[sample.id] = sample
        })
      })
    )
  }, [initialSamples])

  // ADD/REMOVE HANDLERS

  const handleAdd = async () => {
    if (candidateSamples.length === 0) return
    const targetIds = candidateSamples.map(({ id }) => id)
    try {
      setIsOperating(true)
      while (targetIds.length > 0) {
        const sampleIds = targetIds.splice(0, 500) // add 500 per batch
        await addProjectSamples(api, deferredInvalidate)({ projectId: thisProjectId, sampleIds })
        setThisProjectSamples((samples) => [...samples, ...candidateSamples.filter(({ id }) => sampleIds.includes(id))])
      }
      notifications.info(`Added ${candidateSamples.length} ${pluralize(candidateSamples.length, 'sample')} to project`)
    } catch (err) {
      notifications.error(`Failed to add samples to project: ${err}`)
    } finally {
      setIsOperating(false)
    }
    if (onSamplesAdded) onSamplesAdded(candidateSamples)
  }

  const handleRemove = async () => {
    if (candidateSamples.length === 0) return
    const targetIds = candidateSamples.map(({ id }) => id)
    try {
      setIsOperating(true)
      while (targetIds.length > 0) {
        const sampleIds = targetIds.splice(0, 500) // remove 500 per batch
        await removeProjectSamples(api, deferredInvalidate)({ projectId: thisProjectId, sampleIds })
        setThisProjectSamples((samples) => samples.filter(({ id }) => !sampleIds.includes(id)))
      }
      notifications.info(
        `Removed ${candidateSamples.length} ${pluralize(candidateSamples.length, 'sample')} from project`
      )
    } catch (err) {
      notifications.error(`Failed to remove samples from project: ${err}`)
    } finally {
      setIsOperating(false)
    }
    if (onSamplesRemoved) onSamplesRemoved(candidateSamples)
  }

  // FETCHING AND FILTERS

  useEffect(() => {
    // setup
    const criteriaFilters: typeof displayFilters = []
    const thisProjectFilters: typeof displayFilters = []
    const sourcePromises: Promise<MinimalSample[]>[] = []
    let cancelled = false

    // source: box
    if (boxCriterion) {
      criteriaFilters.push({
        id: 'boxCriterion',
        label: boxCriterion.chipLabel,
        filterFn: identity,
        onDelete: () => setBoxCriteron(null),
      })
      sourcePromises.push(getSamplesByBoxId(boxCriterion.id))
    }

    // source: customer
    if (organizationCriterion) {
      criteriaFilters.push({
        id: 'organizationCriterion',
        label: organizationCriterion.chipLabel,
        filterFn: identity,
        onDelete: () => setOrganizationCriterion(null),
      })
      sourcePromises.push(getSamplesByOrganizationId(organizationCriterion.id))
    }

    // source: project
    if (projectCriterion) {
      criteriaFilters.push({
        id: 'projectCriterion',
        label: projectCriterion.chipLabel,
        filterFn: identity,
        onDelete: () => setProjectCriterion(null),
      })
      sourcePromises.push(getSamplesByProjectId(projectCriterion.id))
    }

    // source: taxon
    if (taxonCriteria) {
      criteriaFilters.push({
        id: 'taxonCriteria',
        label: taxonCriteria.chipLabel,
        filterFn: identity,
        onDelete: () => setTaxonCriteria(null),
      })
      sourcePromises.push(getSamplesByTaxonId(taxonCriteria.id))
    }

    // source: US State
    if (usStateCriterion) {
      criteriaFilters.push({
        id: 'usStateCriterion',
        label: usStateCriterion.chipLabel,
        filterFn: identity,
        onDelete: () => setUsStateCriterion(null),
      })
      sourcePromises.push(getSamplesByUsStateId(usStateCriterion.id))
    }

    // source: sample IDs
    if (sampleIdsCriterion.length > 0) {
      const onDelete = () => setSampleIdsCriterion([])
      if (sampleIdsCriterion.length === 1)
        criteriaFilters.push({
          id: 'sampleIds',
          label: `ID is ${sampleIdsCriterion[0]}`,
          filterFn: identity,
          onDelete,
        })
      if (sampleIdsCriterion.length > 1)
        criteriaFilters.push({
          id: 'sampleIds',
          label: `ID is in list (${sampleIdsCriterion.length} samples)`,
          filterFn: identity,
          onDelete,
        })

      const usefulPromises: (MinimalSample | Promise<MinimalSample | undefined>)[] = []
      const fetchIds: number[] = []
      sampleIdsCriterion.forEach((needId) => {
        if (minimalSamplesById[needId]) {
          usefulPromises.push(minimalSamplesById[needId])
        } else {
          fetchIds.push(needId)
        }
      })
      const fetchPromise = fetchIds.length > 0 ? fetchMinimalSamplesByIds(api)(fetchIds) : Promise.resolve([])
      const newOrderedSamplePromises = fetchIds.map((fetchId) =>
        fetchPromise.then((fetched) => fetched.find(({ id }) => id === fetchId))
      )
      setMinimalSamplesById(
        produce((draft) => {
          fetchIds.forEach((fetchId, index) => {
            draft[fetchId] = newOrderedSamplePromises[index]
          })
        })
      )
      sourcePromises.push(
        Promise.all([...usefulPromises, ...newOrderedSamplePromises])
          .then((resolvedSamplesOrUndefineds) => resolvedSamplesOrUndefineds.filter(identity) as MinimalSample[])
          .catch((err) => {
            notifications.error(`Failed to retrieve samples by ID: ${err}`)
            setMinimalSamplesById(
              produce((draft) => {
                fetchIds.forEach((fetchId) => {
                  delete draft[fetchId] // clear promise so failure isn't permanent with the ID
                })
              })
            )
            return []
          })
      )
    }

    const isAddModeWithoutSource = isAddMode && sourcePromises.length === 0

    // post-sources filter: sample type
    if (typeCriterion) {
      criteriaFilters.push({
        id: 'typeCriterion',
        label: typeCriterion.chipLabel,
        filterFn: ({ type }) => type === typeCriterion.id,
        onDelete: () => setTypeCriterion(null),
        chipColor: isAddModeWithoutSource ? 'warn' : 'default',
      })
    }

    // post-sources filter: sample method
    if (methodCriterion) {
      criteriaFilters.push({
        id: 'methodCriterion',
        label: methodCriterion.chipLabel,
        filterFn: ({ method }) => method === methodCriterion.id,
        onDelete: () => setMethodCriterion(null),
        chipColor: isAddModeWithoutSource ? 'warn' : 'default',
      })
    }

    // post-sources filter: date range
    if (fromDate && toDate) {
      criteriaFilters.push({
        id: 'dateCriteria',
        label: `sampled ${fromDate} to ${toDate}`,
        filterFn: ({ sampleDate }) => sampleDate >= fromDate && sampleDate <= toDate,
        onDelete: () => {
          setFromDate(null)
          setToDate(null)
        },
        chipColor: isAddModeWithoutSource ? 'warn' : 'default',
      })
    }

    // this project filter
    if (criteriaFilters.length > 0) {
      const thisProjectSampleIds = thisProjectSamples.map(({ id }) => id)
      if (isAddMode) {
        thisProjectFilters.push({
          id: 'thisProject',
          label: 'not already in this project',
          filterFn: ({ id }) => !thisProjectSampleIds.includes(id),
        })
      } else {
        thisProjectFilters.push({
          id: 'thisProject',
          label: 'currently in this project',
          filterFn: ({ id }) => thisProjectSampleIds.includes(id),
        })
      }
    }

    // set display filters
    setDisplayFilters([...criteriaFilters, ...thisProjectFilters])

    // clear matched samples
    setCandidateSamples([])

    // retrieve and filter data
    if (sourcePromises.length === 0) {
      // nothing to fetch
      if (criteriaFilters.length === 0) {
        // still waiting for any criterion
        setResultsMessage(null)
      } else if (isAddMode) {
        // add mode with post-source filter(s) only; need more criteria
        setResultsMessage(
          <span>
            Add more criteria to match samples. (Type, Method, and Date Range may only be used as supplemental criteria
            when adding samples.)
          </span>
        )
      } else {
        // remove mode with post-source filter(s) only; use current project samples as sole source
        const filteredSamples = criteriaFilters.reduce<MinimalSample[]>(
          (acc, { filterFn }) => acc.filter(filterFn),
          thisProjectSamples
        )
        setCandidateSamples(filteredSamples)
        const operableCount = filteredSamples.length
        setResultsMessage(
          <span>
            Matched <strong>{operableCount}</strong> {pluralize(operableCount, 'sample')} currently in your project.
          </span>
        )
      }
    } else {
      // enter fetching mode
      setResultsMessage(<LinearProgress css={{ width: '25rem' }} />)

      // process sources and filters
      Promise.all(sourcePromises).then((sources) => {
        if (cancelled) return // do nothing if this effect has been superceded

        // intersect sources
        const [firstSource, ...restSources] = sources
        let filteredSamples = restSources.reduce(
          (acc, source) => intersectionBy(acc, source, ({ id }) => id),
          firstSource
        )

        // apply filters
        filteredSamples = criteriaFilters.reduce<MinimalSample[]>(
          (acc, { filterFn }) => acc.filter(filterFn),
          filteredSamples
        )
        const matchedCount = filteredSamples.length // get match count before project-based filter
        filteredSamples = thisProjectFilters.reduce<MinimalSample[]>(
          (acc, { filterFn }) => acc.filter(filterFn),
          filteredSamples
        )

        // set candidate samples and messaging
        setCandidateSamples(filteredSamples)
        const operableCount = filteredSamples.length
        setResultsMessage(
          <span>
            Matched {matchedCount} {pluralize(matchedCount, 'sample')} of which <strong>{operableCount}</strong>{' '}
            {pluralize(operableCount, 'is')} {isAddMode ? 'not already' : 'currently'} in your project.
          </span>
        )
      })
    }

    // return cleanup function
    return () => {
      cancelled = true
    }
  }, [
    thisProjectSamples,
    thisProjectName,
    isAddMode,

    // criteria
    boxCriterion,
    organizationCriterion,
    projectCriterion,
    sampleIdsCriterion,
    taxonCriteria,
    usStateCriterion,
    typeCriterion,
    methodCriterion,
    fromDate,
    toDate,
  ])

  // RENDER

  const isBothDatesError = !!fromDate && !!toDate && fromDate > toDate

  return (
    <div>
      <div css={{ display: 'flex', alignItems: 'center' }}>
        <Title>
          Edit Samples in Project {thisProjectName}{' '}
          <Title.Subtitle>
            ({thisProjectSamples.length} {pluralize(thisProjectSamples.length, 'sample')})
          </Title.Subtitle>
        </Title>
        <ToggleButtonGroup
          css={{ marginLeft: 'auto' }}
          value={mode}
          onChange={(_, newMode) => setMode(newMode)}
          exclusive
        >
          <ToggleButton size="small" value={'add'} key="add" css={{ width: '6em' }}>
            Add
          </ToggleButton>
          <ToggleButton size="small" value={'remove'} key="remove" css={{ width: '6em' }}>
            Remove
          </ToggleButton>
        </ToggleButtonGroup>
      </div>
      <div
        css={{
          marginTop: '1rem',
          display: 'grid',
          gap: '1rem',
          gridTemplateColumns: '1fr 1fr',
          gridTemplateAreas: `
            "boxCriterion           boxCriterion"
            "organizationCriterion  organizationCriterion"
            "projectCriterion       projectCriterion"
            "taxonCriterion         taxonCriterion"
            "usStateCriterion       usStateCriterion"
            "sampleIdsCriterion     sampleIdsCriterion"
            "typeCriterion          methodCriterion"
            "fromDate               toDate"
          `,
        }}
      >
        <BoxCriterionField gridArea="boxCriterion" value={boxCriterion} onChange={setBoxCriteron} />
        <OrganizationCriterionField
          gridArea="organizationCriterion"
          value={organizationCriterion}
          onChange={setOrganizationCriterion}
        />
        <ProjectCriterionField
          gridArea="projectCriterion"
          value={projectCriterion}
          onChange={setProjectCriterion}
          thisProjectId={thisProjectId}
        />
        <TaxonomyCriterionField gridArea="taxonCriterion" value={taxonCriteria} onChange={setTaxonCriteria} />
        <UsStateCriterionField gridArea="usStateCriterion" value={usStateCriterion} onChange={setUsStateCriterion} />
        <SampleIdsCriterionField
          gridArea="sampleIdsCriterion"
          value={sampleIdsCriterion}
          onChange={setSampleIdsCriterion}
        />
        <TypeCriterionField gridArea="typeCriterion" value={typeCriterion} onChange={setTypeCriterion} />
        <MethodCriterionField gridArea="methodCriterion" value={methodCriterion} onChange={setMethodCriterion} />
        <DateField
          gridArea="fromDate"
          value={fromDate}
          onChange={setFromDate}
          label="From Date"
          error={(toDate && !fromDate) || isBothDatesError}
        />
        <DateField
          gridArea="toDate"
          value={toDate}
          onChange={setToDate}
          label="To Date"
          error={(fromDate && !toDate) || isBothDatesError}
        />
      </div>
      <div
        css={{
          marginTop: '1rem',
          display: 'grid',
          gap: '1rem',
        }}
      >
        <FilterChips filters={displayFilters} joinLabel="and" noneNode={<Chip label="No criteria" />} />
        <div css={{ minHeight: '1.2rem', display: 'flex', alignItems: 'center' }}>{resultsMessage}</div>
        <div css={{ display: 'flex' }}></div>
        <MuiButton
          variant="contained"
          disabled={candidateSamples.length === 0 || isOperating}
          onClick={isAddMode ? handleAdd : handleRemove}
          startIcon={isAddMode ? <AddCircleIcon /> : <RemoveCircleIcon />}
        >
          {(() => {
            if (isAddMode) {
              if (isOperating) return 'Adding samples to project...'
              if (candidateSamples.length === 0) return 'Add samples to project'
              return `Add ${candidateSamples.length} ${pluralize(candidateSamples.length, 'sample')} to project`
            } else {
              if (isOperating) return 'Removing samples from project...'
              if (candidateSamples.length === 0) return 'Remove samples from project'
              return `Remove ${candidateSamples.length} ${pluralize(candidateSamples.length, 'sample')} from project`
            }
          })()}
        </MuiButton>
      </div>
    </div>
  )
}
